<script>
import Layout from '../../layouts/main'
import PageHeader from '../../../components/page-header'
/**
 * Starter component
 */
export default {
  page: {
    title: 'LINE Messageing API',
    meta: [{name: 'description'}]
  },
  components: { Layout, PageHeader },
  data(){
    return {
      title: 'LINE Bot',
      
    }
  }
}
</script>

<template>
  <Layout>
       <PageHeader :title="title"/>
     <div class="row">
      <div class="col-lg-6">
        <div class="card">
          <div class="card-body">
              LINE
              </div>
        </div>
      </div>
     </div>
  </Layout>
</template>



